import Vue from 'vue'
import Vuex from 'vuex'
import router from '../router'

import { addDoc, setDoc, doc, onAuthStateChanged, signInWithEmailAndPassword,createUserWithEmailAndPassword, auth, collection, onSnapshot, getFirestore, query } from '../firebase/config'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    /* this variable store all firestore database */
    myFirestore: [],
    loggedUser: undefined,


  },
  getters: {

    getMyUserData: (state) => () => {
      try {
        var myUserData = state.myFirestore.filter(data => data.userAccount.toLowerCase() == state.loggedUser);
        return myUserData[0]
      } catch (error) {
        return
      }

    },
    getAnotherUsers: (state) => () => {
      try {
        var AnotherUsersData = state.myFirestore.filter(data => data.userAccount.toLowerCase() != state.loggedUser);
        return AnotherUsersData
      } catch (error) {
        return
      }

    },
    getAcceptedMatch: (state) => () => {
      try {
        var myUserData = state.myFirestore.filter(data => data.userAccount.toLowerCase() == state.loggedUser);
        var otherUsersData = state.myFirestore.filter(data => data.userAccount.toLowerCase() != state.loggedUser);

        var acceptedDocuments = myUserData[0].data.user.matchUsers.filter(matches => matches.state == "accepted");
        var acceptedDocumentsArray = acceptedDocuments.map(element => element.document);
        var responseObject = otherUsersData.filter(element => acceptedDocumentsArray.includes(element.documentid))

        return responseObject
      } catch (error) {
        return
      }

    },
    getPendingMatch: (state) => () => {
      try {
        var myUserData = state.myFirestore.filter(data => data.userAccount.toLowerCase() == state.loggedUser);
        var otherUsersData = state.myFirestore.filter(data => data.userAccount.toLowerCase() != state.loggedUser);

        var acceptedDocuments = myUserData[0].data.user.matchUsers.filter(matches => matches.state == "pending");
        var acceptedDocumentsArray = acceptedDocuments.map(element => element.document);
        var responseObject = otherUsersData.filter(element => acceptedDocumentsArray.includes(element.documentid))
        return responseObject
      } catch (error) {
        return
      }

    },
    getNotMatchedUsers: (state) => () => {

      try {
        var myUserData = state.myFirestore.filter(data => data.userAccount.toLowerCase() == state.loggedUser);
        var otherUsersData = state.myFirestore.filter(data => data.userAccount.toLowerCase() != state.loggedUser);

        var auxArray = myUserData[0].data.user.matchUsers;

        var interactions = auxArray.map(element => element.document);

        var retArray = otherUsersData.filter(element => !interactions.includes(element.documentid))

        return retArray
      } catch (error) {
        return
      }

    },
    getConversation: (state) => (mail) => {

      try {
        var myUserData = state.myFirestore.filter(data => data.userAccount.toLowerCase() == state.loggedUser);
        var chatIndex = myUserData[0].data.user.matchUsers.findIndex(matches => matches.matchUser == mail);
        var responseObject = myUserData[0].data.user.matchUsers[chatIndex].chat
        var document = myUserData[0].data.user.matchUsers[chatIndex].document
        return { chat: responseObject, document: document }
      } catch (error) {
        return
      }

    },
  },
  mutations: {

    /* this mutation set all firestoredata into myFirestore variable */
    setMyFirestore(state, fs) {
      state.myFirestore = []
      try {
        fs.forEach((doc) => {
          state.myFirestore.push({ documentid: doc.id, data: doc.data(), userAccount: doc.data().user.userMail })
        });
      } catch (error) {
        return
      }

    },
    /* this mutatiion set the current logged user into state to manipulate data getters*/
    setUser(state, payload) {
      state.loggedUser = payload.email
    },
  },
  actions: {
    /* firestore actions */
    async instanceFirestore({ commit }) {
      const db = getFirestore();
      const q = query(collection(db, "starchat"));
      onSnapshot(q, (querySnapshot) => {
        commit('setMyFirestore', querySnapshot)
      });
    },
    
    /* session actions      */
    async signIn(context, { email, password }) {
      await signInWithEmailAndPassword(auth, email, password).then( () => {
        router.push("/panel");
      
      
      }).catch(err => {
        let message = 'An error has occured!';

        if (err.code === 'auth/invalid-email') {
          message = 'Invalid email address!';
          alert(message)

        } else if (err.code === 'auth/user-not-found') {
          alert(message)

          message = 'No account with such credentials!';
        } else if (err.code === 'auth/wrong-password') {
          message = 'Incorrect password! Try again.';
          alert(message)
        }

        throw new Error(message);
      });
    },

    async signUp(context, { email, password,newUser }) {
      await createUserWithEmailAndPassword(auth, email, password).then( async () => {
        
        await addDoc(collection(getFirestore(), "starchat"), newUser);
        router.push("/panel")
    
    }).catch(err => {
        let message = 'An error has occured!';

        alert(message)
        throw new Error(err);
      });

    },

    async signOut(context) {
      const res = await auth.signOut()
        .then(() => {router.push("/")
        });
      if (res) {
        context.commit('setUser', res.user)
      }
    },

    async getCurrentUser(context) {
      await onAuthStateChanged(auth, (user) => {
        if (user) {
          context.commit('setUser', user)
        }
      })
    },
    /* match actions */
    async sendMatch(commit, objeto) {
      if (objeto != "") {
        console.log(objeto)
        const db = getFirestore();
        if (objeto.fromUser.data.user.matchCounter > 0) {

          objeto.fromUser.data.user.matchCounter -= 1;
          objeto.toUser.data.user.matchUsers.push({ matchUser: objeto.fromUser.data.user.userMail, state: "pending", document: objeto.fromUser.documentid })
          objeto.fromUser.data.user.matchUsers.push({ matchUser: objeto.toUser.data.user.userMail, state: "sent", document: objeto.toUser.documentid })
          await setDoc(doc(db, "starchat", objeto.fromUser.documentid), objeto.fromUser.data);
          await setDoc(doc(db, "starchat", objeto.toUser.documentid), objeto.toUser.data);

        }

        commit
      }
    },
    async approveMatch(commit, objeto) {
      if (objeto != "") {
        const db = getFirestore();
        var fromIndex = objeto.fromUser.data.user.matchUsers.findIndex(element => element.matchUser == objeto.toUser.data.user.userMail)
        var toIndex = objeto.toUser.data.user.matchUsers.findIndex(element => element.matchUser == objeto.fromUser.data.user.userMail)

        objeto.toUser.data.user.matchUsers[toIndex].state = "accepted"
        objeto.fromUser.data.user.matchUsers[fromIndex].state = "accepted"

        await setDoc(doc(db, "starchat", objeto.fromUser.documentid), objeto.fromUser.data);
        await setDoc(doc(db, "starchat", objeto.toUser.documentid), objeto.toUser.data);



        commit
      }
    },
    async rejectMatch(commit, objeto) {
      if (objeto != "") {
        const db = getFirestore();
        var fromIndex = objeto.fromUser.data.user.matchUsers.findIndex(element => element.matchUser == objeto.toUser.data.user.userMail)
        var toIndex = objeto.toUser.data.user.matchUsers.findIndex(element => element.matchUser == objeto.fromUser.data.user.userMail)

        objeto.toUser.data.user.matchUsers[toIndex].state = "rejected"
        objeto.fromUser.data.user.matchUsers[fromIndex].state = "rejected"

        await setDoc(doc(db, "starchat", objeto.fromUser.documentid), objeto.fromUser.data);
        await setDoc(doc(db, "starchat", objeto.toUser.documentid), objeto.toUser.data);



        commit
      }

    },
    /* chat actions */
    async sendMessage(commit, objeto) {
      if (objeto != "") {
        const db = getFirestore();


        var myUserData = this.state.myFirestore.filter(data => data.userAccount.toLowerCase() == this.state.loggedUser.toLowerCase());
        console.log("rescate mi data")

        var fromIndex = myUserData[0].data.user.matchUsers.findIndex(element => element.matchUser == objeto.to)
        console.log("rescate mi indice")

        var targetDocId = myUserData[0].data.user.matchUsers[fromIndex].document
        console.log("rescate el target doc")

        var targetData = this.state.myFirestore.filter(data => data.documentid == targetDocId);
        console.log("rescate el target data")
        console.log(targetData)

        var toIndex = targetData[0].data.user.matchUsers.findIndex(element => element.matchUser.toLowerCase() == this.state.loggedUser.toLowerCase())
        console.log(this.state.loggedUser)
        console.log(toIndex)

        if (myUserData[0].data.user.matchUsers[fromIndex].chat == undefined) {

          myUserData[0].data.user.matchUsers[fromIndex].chat = []
          targetData[0].data.user.matchUsers[toIndex].chat = []
        }

        var puchObject = { text: objeto.text, from: objeto.from, to: objeto.to, date: objeto.date }

        myUserData[0].data.user.matchUsers[fromIndex].chat.push(puchObject)
        console.log("hice push de la data del chat a mi data")
        console.log(targetData[0].data.user.matchUsers[toIndex])
        targetData[0].data.user.matchUsers[toIndex].chat.push(puchObject)
        console.log("hice push de la data del chat a la data de mi objetivo")

        await setDoc(doc(db, "starchat", myUserData[0].documentid), myUserData[0].data);
        await setDoc(doc(db, "starchat", targetData[0].documentid), targetData[0].data);

        commit

      }
    },
  },



  modules: {
  }
})
